.card { 
    width: 550px;
    height: 868px;
    background-color: #23252C;
    margin: 53px 25px 0 25px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.card__content {
    width: 317px;
    height: 780px;
    background-color: #fff;
    color: #fff;
    border-radius: 10px;
    background-color: #1A1B21;
    position: relative;
}

.card__content .followButtons{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 10px;
    cursor: pointer;
}

.card__content .followButtons button{
    width: 117px;
    height: 37px;
    cursor: pointer;
    border-radius: 8px;
    text-align: center;
    border-width: 1px;
    border-color: #D1D5DB;
    text-decoration: none;
}

.card__content .followButtons button a {
    text-decoration: none;
}

/* .card__content .followButtons button img {
    width: 13px;
    height: 10px;
    padding-left: 3px;
} */

.card__contentInfoHeader {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
    text-align: center;
}

.card__contentInfoHeader h2 {
    font-weight: bolder;
}

.card__contentInfoHeader span {
    font-size: 1.3em;
    color: #F3BF99;
}

.card__content p {
    font-size: 10px;
    line-height: 20px;
    font-weight: 500;
}

.card__contentInfoBody { 
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 5px;
    align-items: center;
    text-align: center;
}

.card__contentInfoBody .__contentBodyBox {
    display: block;
    max-width: 80%;
    text-align: start;
}

.card__contentInfoBody .__contentBodyBox h4 {
    margin-top: 15px;
}

.card__socials {
    width: 100%;
    height: 9vh;
    border-radius: 0 0 10px 10px;
    background-color: #161619;
    position: absolute;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.card__socials img {
    margin: 10px;
    cursor: pointer;
}   

.card__socials img:hover {
    filter: invert(1);
}

@media (max-width: 600px) {
    .card {
        max-width: 90vw;
        max-height: 868px;
        background-color: #23252C;
        margin: 53px 25px 0 25px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

